.cards {
  padding-left: 8px;
  padding-right: 8px;
}
@media (min-width: 768px) {
  .cards {
    padding: 0;
  }
}
.cards__inner {
  display: flex;
  flex-wrap: wrap;
  margin: -4px;
  position: relative;
  overflow: hidden;
}
.cards__card {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: calc(33.3333333333% - 8px);
  margin: 4px;
  padding: 16px 8px;
  border-radius: 16px;
  cursor: pointer;
  transition: 0.2s ease;
}
.cards__card:hover {
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.1);
}
.cards__card:nth-child(n+7) {
  position: absolute;
  transform: translate(-100%);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}
.cards__card:nth-child(n+9) {
  transition: 0.4s ease;
}
.cards__card:nth-child(n+18) {
  transition: 0.8s ease;
}
@media (min-width: 768px) {
  .cards__card {
    width: calc(16.6666666667% - 8px);
  }
}
@media (min-width: 1280px) {
  .cards__card {
    width: calc(11.1111111111% - 8px);
  }
  .cards__card:nth-child(7), .cards__card:nth-child(8), .cards__card:nth-child(9) {
    position: relative;
    transform: none;
    visibility: visible;
    opacity: 1;
    pointer-events: all;
  }
}
.cards__card.gray {
  background-color: #E7ECF3;
}
.cards__card.violet {
  background-color: #F2D7FF;
}
.cards__card.green {
  background-color: #C3FFC9;
}
.cards__card.yellow {
  background-color: #FFEDCC;
}
.cards__card.pink {
  background-color: #FFE1E1;
}
.cards__card.blue {
  background-color: #D9E8FB;
}
.cards__card.fuchsia {
  background-color: #FFE1F0;
}
.cards__card.peach {
  background-color: #FFE1E1;
}
.cards__card.aqua {
  background-color: #C3FFF4;
}
.cards__img {
  display: block;
  width: 72.7%;
  aspect-ratio: 1/1;
  padding: 8px;
  object-fit: contain;
  margin-bottom: 4px;
}
@media (min-width: 768px) {
  .cards__img {
    width: 65.61%;
  }
}
@media (min-width: 1280px) {
  .cards__img {
    width: 61%;
  }
}
.cards__title {
  font-size: 12px;
  font-weight: 500;
  line-height: 150%;
}
.cards .isShow .cards__card {
  position: relative;
  transform: none;
  pointer-events: all;
  opacity: 1;
  visibility: visible;
}

